body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  max-height: 100vh;
  color: white;
  overflow: auto;
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
}
.whiteText {
  color: white;
}
.progressbar {
  width: 100%;
}
.expansionPanel {
  width: 100%;
}
.listItemText {
  font-size: 2rem;
}
.quizPageWrapper {
  min-height: 100vh;
  max-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-left: 8px;
  padding-right: 8px;
}
.resultPageWrapper {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.theWord {
  font-size: 8em;
  margin: 0px;
  padding: 0px;
  text-shadow: 3px 5px 7px black;
  font-weight: 300;
}
.theWordSmall {
  margin: 0px;
  padding: 0px;
  text-shadow: 3px 5px 7px black;
  font-weight: 300;
}

.wrong-pinyin {
  color: red;
  font-size: 20px;
}
.wrong-eng {
  color: red;
}
.theWordDiv {
  -webkit-flex: 1 1;
          flex: 1 1;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.optionsDiv {
  display: grid;
  grid-template-columns: 50% 50%;
  width: 100%;
  grid-column-gap: 8px;
  grid-row-gap: 8px;
  overflow: hidden;
  padding-bottom: 8px;
}
.optionButton {
  display: -webkit-flex;
