  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 120px;
  width: 100%;
}

.betaTag {
  position: fixed;
  background-color: #b53f3f;
  right: -55px;
  padding: 5px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 200px;
  text-align: center;
  text-shadow: 1px 1px 1px black;
  box-shadow: 0 1px 3px 1px #17191d;
}
.beta {
  padding-left: 50px;
}
.versionNr {
  font-size: 10px;
}

.pickChapterTitle {
